.navbar {
    width: 100%;
    height: 50px;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2px 20px;
}
.logo {
    color: white;
    font-size: 20px;
}
.navbar ul {
    display: flex;
    gap: 10px;
}
.navbar ul li {
    list-style-type: none;
}
.navbar ul li  a{
    color: white;
    text-decoration: none;
}
.cart-count {
    background-color: red;
    padding: 6px;
    border-radius: 50%;
    height: 24px;
    width: 20px;
    font-size: 13px;
    text-align: center;
    display: inline-block;

}