.cart-container {
    display: flex;
    padding: 40px;
    text-align: center;
    flex-wrap: wrap;
}
.cart-product {
    display: flex;
    gap: 5px;
    align-items: center;
    margin-bottom: 10px;
    border: 3px solid black;
    margin-left: 130px;
    width: 400px;
}
.cart-product .img {
    width:100px;
    height: 100px;
}
.cart-product .img img {
    width: 100%;
    height: 100%;
}
.cart-heading {
    font-size: 40px;
    font-weight: 600;
    text-align: center;
    color:rgb(10, 247, 109);
    border-bottom: 4px solid black;
}
.cart-amount {
    font-size: 35px;
    font-weight: 600;
    text-align: center;
    color: rgb(18, 18, 19);
    background-color: rgb(10, 247, 156);
    border-radius: 10px;
    margin: 2% 15%;
    width: 350px;
    padding: 15px;
}