.product {
    width: 200px;
    height: 250px;
    border: 3px solid black;
}
.product .img {
    width: 100%;
    height: 150px;
}
.product .img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.details {
    padding: 5px ;
    font-size: 14px;
}
.details h3 {
    margin-bottom: 10px;
}
.details button {
    padding: 5px;
    margin-top: 5px;
    background-color: teal;
    color: white;
    border: none;
    border-radius: 5px;
}
.details button:hover {
  background-color: rgb(2, 83, 83);
}
.details p {
    font-size: 13px;
    font-weight: 500;
    color:rgb(32, 32, 32)
}
.btn-remove {
    background-color: red !important;
}